@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:"";content:none}table{border-collapse:collapse;border-spacing:0}

html{box-sizing:border-box;height:100%;-webkit-tap-highlight-color:rgba(0,0,0,0);scroll-behavior:smooth}*,*:before,*:after{box-sizing:inherit}input:-webkit-autofill,input:-webkit-autofill:hover,input:-webkit-autofill:focus,textarea:-webkit-autofill,textarea:-webkit-autofill:hover,textarea:-webkit-autofill:focus,select:-webkit-autofill,select:-webkit-autofill:hover,select:-webkit-autofill:focus{-webkit-transition:background-color 5000s ease-in-out 0s;transition:background-color 5000s ease-in-out 0s}input::-ms-clear{display:none;width:0;height:0}input::-ms-reveal{display:none;width:0;height:0}body{position:relative;min-height:100%;overflow-x:hidden;color:#4E5458;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;font-family:'Roboto', sans-serif}input,textarea{font-family:inherit;-webkit-appearance:none;-moz-appearance:none;appearance:none}input::-webkit-input-placeholder, textarea::-webkit-input-placeholder{color:#A5AAB2}input:-ms-input-placeholder, textarea:-ms-input-placeholder{color:#A5AAB2}input::-ms-input-placeholder, textarea::-ms-input-placeholder{color:#A5AAB2}input::placeholder,textarea::placeholder{color:#A5AAB2}input:focus,textarea:focus{outline:0}.ellipsis{white-space:nowrap;overflow:hidden;text-overflow:ellipsis}

body{justify-content:center;display:flex;padding:40px 15px}main{max-width:400px}.search{box-shadow:1px 1px 8px #cecece;border-radius:4px;overflow:hidden;border:solid 1px #e0e0e5}.hint{font-size:20px}.hint-details{font-size:14px;margin-top:10px;margin-bottom:20px;line-height:1.4}


.search-bar{height:44px;background-color:white;display:flex;padding:0 12px;align-items:center}.search-bar__input{border:0;line-height:1.43;font-size:14px;flex:1 1;color:#4E5458}.search-bar__icon-search{margin-right:12px}.search-bar__icon-cross{margin-left:12px}.search-bar__icon-search,.search-bar__icon-cross{font-size:16px;color:#7D807A}

.search-suggestion{font-size:14px;border-top:1px solid #f0f0f5;padding:17px 15px;display:flex;flex-direction:column;cursor:pointer}.search-suggestion.focus{background:#FFFEDC}.search-suggestion__highlight{color:#7DACE9}.search-suggestion__title{font-size:14px}.search-suggestion__subtitle,.search-suggestion__list,.search-suggestion__description{color:#7D807A;margin:5px 0;font-size:12px}.search-suggestion__list{border-top:1px solid #f0f0f5;border-bottom:1px solid #f0f0f5;padding:5px 0}.search-suggestion__list i{font-size:5px;color:#7DACE9;margin-right:3px}.search-suggestion__list li{color:inherit;display:flex;align-items:center}

.search-suggestions{max-height:300px;overflow:scroll}

.search-no-results{border-top:1px solid #f0f0f5;color:#7D807A;min-height:150px;justify-content:center;align-items:center;display:flex}

